@import url(https://fonts.googleapis.com/css?family=Allura&display=swap);
@import url(https://fonts.googleapis.com/css?family=Manjari&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.createEditDelete {
  display: inline-flex;
  width: 100%;
  margin-top: 20px;
  justify-content: space-around;
}

#notesParent {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  border: none;
}

.noteTitles {
  list-style: none;
  background: rgb(141, 166, 233);
  border-radius: 15px;
  color: rgb(72, 104, 192);
  width: 200px;
  height: 300px;
  font-size: 120%;
  font-family: 'Manjari', sans-serif;
}
.noteTitles:hover {
  cursor: pointer;
  background: rgb(184, 203, 253);
}

#noteAuthors {
  font-size: 110%;
  font-family: 'Allura', cursive;
  -webkit-font-feature-settings: "kern" off;
          font-feature-settings: "kern" off;
  font-variant: none;
  color: white;
  list-style: none;
}

.titlesList {
  text-align: center;
}

.editTitle {
  text-align: center;
  font-size: 30px;
}

.deleteTitle {
  text-align: center;
  font-size: 30px;
}

.inputTitle, .inputText {
  text-align: center;
  padding: 8px;
  width: 70%;
}

.inputHolder {
  margin: 0 auto;
  text-align: center;
  
}

.submit {
  margin-top: 30px;
}

.forPadding {
  padding-bottom: 40px;
}
